.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Utility Classes */
.container {
  max-width: auto;
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}

.btn {
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  display: inline-block;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.2),
    transparent
  );
  transition: left 0.5s ease;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: var(--gradient-primary);
  color: var(--white);
  box-shadow: 0 4px 12px rgba(46, 107, 111, 0.3);
}

.btn-primary:hover {
  background: linear-gradient(
    135deg,
    var(--teal-light) 0%,
    var(--primary-teal) 100%
  );
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(46, 107, 111, 0.4);
}

.btn-secondary {
  background: var(--gradient-secondary);
  color: var(--white);
  box-shadow: 0 4px 12px rgba(160, 49, 45, 0.3);
}

.btn-secondary:hover {
  background: linear-gradient(
    135deg,
    var(--red-light) 0%,
    var(--primary-red) 100%
  );
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(160, 49, 45, 0.4);
}

.btn-gradient {
  background: var(--gradient-mixed);
  color: var(--white);
  box-shadow:
    0 4px 12px rgba(46, 107, 111, 0.25),
    0 4px 12px rgba(160, 49, 45, 0.25);
}

.btn-gradient:hover {
  background: var(--gradient-mixed-reverse);
  transform: translateY(-3px);
  box-shadow:
    0 6px 20px rgba(46, 107, 111, 0.35),
    0 6px 20px rgba(160, 49, 45, 0.35);
}

.btn-gradient-diagonal {
  background: var(--gradient-mixed-diagonal);
  color: var(--white);
  box-shadow:
    0 4px 12px rgba(46, 107, 111, 0.25),
    0 4px 12px rgba(160, 49, 45, 0.25);
}

.btn-gradient-diagonal:hover {
  background: linear-gradient(
    135deg,
    var(--primary-red) 0%,
    var(--red-light) 50%,
    var(--primary-teal) 100%
  );
  transform: translateY(-3px) scale(1.02);
  box-shadow:
    0 6px 20px rgba(46, 107, 111, 0.35),
    0 6px 20px rgba(160, 49, 45, 0.35);
}

.btn-outline {
  background-color: transparent;
  border: 2px solid var(--primary-teal);
  color: var(--primary-teal);
}

.btn-outline:hover {
  background: var(--gradient-primary);
  color: var(--white);
  border-color: transparent;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(46, 107, 111, 0.3);
}

.btn-outline-red {
  background-color: transparent;
  border: 2px solid var(--primary-red);
  color: var(--primary-red);
}

.btn-outline-red:hover {
  background: var(--gradient-secondary);
  color: var(--white);
  border-color: transparent;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(160, 49, 45, 0.3);
}

/* Loading Spinner */
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400px;
}

.spinner {
  border: 4px solid var(--gray-medium);
  border-top: 4px solid var(--primary-teal);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .container {
    padding: 0 15px;
  }

  .btn {
    padding: 10px 20px;
    font-size: 14px;
  }
}
